import React, { Component } from 'react';
import Slider from 'react-rangeslider';
import 'react-rangeslider/lib/index.css';
class UDRangeSlider extends React.Component {
  constructor(props, context) {
    super(props, context)
    this.handleChange = this.handleChange.bind(this);
    this.state = {
      value: 0,
      hidden: false
    }
  }
  componentWillMount() {
    this.pubSubToken = PubSub.subscribe(this.props.id, this.onIncomingEvent.bind(this));
  }

  componentWillUnmount() {
    PubSub.unsubscribe(this.pubSubToken);
  }

  onIncomingEvent(eventName, event) {
    if (event.type === "requestState") {
      var data = {
        attributes: {
          value: this.state.value,
          hidden: this.state.hidden,
        }
      }
      UniversalDashboard.post(`/api/internal/component/element/sessionState/${event.requestId}`, data);
    }
    else if (event.type === "setState") {
      this.setState(event.state.attributes);
    }
    else if (event.type === "clearElement") {
      this.setState({
        value: 0
      });
    }
    else if (event.type === "removeElement") {
      this.setState({
        hidden: true
      });
    }
  }
  onChange(e) {
    this.setState({ value: e });
    if (this.props.onChange == null) {
      return
    }
    if (this.props.activeOnChange) {
      UniversalDashboard.publish("element-event", {
        type: "clientEvent",
        eventId: this.props.id + "onChange",
        eventName: "onChange",
        eventData: e.value
      });
    }
  }
  handleChange = value => {
    this.setState({
      value: value
    })
  };


  render() {
    if (this.state.hidden) {
      return null;
    }
    const { value } = this.state
    return (
      <div className='slider'>
        <Slider
          orientation={this.props.orientation}
          min={this.props.min}
          max={this.props.max}
          step={this.props.step}
          value={value}
          handleLabel={value}
          onChange={this.handleChange}
        />
      </div>
    )
  }
}

export default UDRangeSlider
